body {
  background-color: #ddd;
  font-family: "Ancizar sans", Tahoma, Geneva, sans-serif;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -webkit-box-direction: normal;
  -webkit-box-orient: vertical;
  -webkit-flex-direction: column;
  -moz-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  overflow-y: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Ancizar serif", "MS Serif", "New York", serif;
}

a {
  color: #2d2656;
}

a:hover {
  text-decoration: underline;
  color: #ec6607;
}

main {
  -webkit-box-flex: 1;
  -webkit-flex: 1 0 auto;
  -moz-box-flex: 1;
  -moz-flex: 1 0 auto;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
}

body > footer:before {
  content: " ";
  display: table;
}

body > footer:after {
  clear: both;
}

blockquote,
div.indent {
  margin: 1em 1.2em;
}

.align-center {
  text-align: center;
}

.align-justify {
  text-align: justify;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.no-padding {
  padding: 0;
}

ol {
  list-style: decimal outside none;
}

li a {
  font-size: 1em;
}

ul,
ol {
  margin-left: 1.2em;
}

#services {
  background: #333;
  position: fixed;
  right: -800px;
  left: auto;
  top: 0;
  -webkit-transition: all 0.25s ease-out;
  -moz-transition: all 0.25s ease-out;
  -o-transition: all 0.25s ease-out;
  transition: all 0.25s ease-out;
  z-index: 20000;
}

#services.active {
  right: 0;
}

#services .navbar-default .navbar-nav > li > a:hover,
#services .navbar-default .navbar-nav > li > a:focus {
  background-color: yellow;
  color: pink;
}

#services ul {
  margin: 0;
  overflow-x: hidden;
  padding: 0 10px;
}

#services ul li {
  border-bottom: 1px solid #222;
  border-top: 1px solid #444;
  width: 240px;
  padding: 10px 0;
  height: 52px;
}

#services ul li:hover {
  background: #444;
}

#services ul li:first-child {
  border-top: 0;
}

#services ul li:last-child {
  border-bottom: 0;
}

#services ul li a {
  color: white;
  font-size: 0.9em;
  line-height: 32px;
  white-space: nowrap;
  display: inline;
  background: transparent;
  padding: 0;
}
footer #services ul li a img {
  display: block;
  float: left;
  height: 32px;
  margin: 0 5px;
  width: 32px;
}

#services .indicator {
  background-color: #2d2656;
  background-image: url(../images/backServices.png);
  background-position: 0 0;
  cursor: pointer;
  height: 126px;
  left: -34px;
  position: absolute;
  top: 150px;
  width: 34px;
}

#services .indicator:hover {
  background-position: 0 -127px;
}

#services .indicator.active {
  background-position: -35px 0;
}

#services .indicator.active:hover {
  background-position: -35px -127px;
}

ul.dropdown-menu {
  min-width: 54px;
  background: #333;
  border-color: #222;
  padding: 0;
}

ul.dropdown-menu a {
  color: white;
  padding: 3px 10px;
}

ul.dropdown-menu a:hover,
ul.dropdown-menu a:active,
ul.dropdown-menu a:focus {
  background: #4b4b4b;
  color: white;
}

